import { useCallback } from "react";
import Particles from "react-tsparticles";
import type { Container, Engine } from "tsparticles-engine";
import { loadSlim } from "tsparticles-slim";

const BackgroundParticles = () => {
    const particlesInit = useCallback(async (engine: Engine) => {
        console.log(engine);
        await loadSlim(engine);
    }, []);

    const particlesLoaded = useCallback(async (container: Container | undefined) => {
        await console.log(container);
    }, []);
    
    // const options: ISourceOptions = useMemo(
    //     () => ({
    //         fullScreen: {
    //             enable: true,
    //             zIndex: -1,
    //         },
    //         background: {
    //             color: {
    //                 value: "#050505FF",
    //             },
    //             repeat: "no-repeat",
    //             size: "cover",
    //         },
    //         fpsLimit: 60,
    //         particles: {
    //             color: {
    //                 value: "#0b364b",
    //             },
    //             links: {
    //                 color: "#0b364b",
    //                 distance: 150,
    //                 enable: true,
    //                 opacity: 0.5,
    //                 width: 1,
    //             },
    //             move: {
    //                 enable: true,
    //                 random: false,
    //                 speed: 0.1,
    //                 straight: false,
    //             },
    //             number: {
    //                 density: {
    //                     enable: true,
    //                 },
    //                 value: 150,
    //             },
    //             opacity: {
    //                 value: { min: 0.2, max: 0.5 },
    //                 animation: {
    //                     enable: true,
    //                     speed: 0.2,
    //                     startValue: "min",
    //                 }
    //             },
    //             shape: {
    //                 type: "circle",
    //             },
    //             size: {
    //                 value: { min: 1, max: 6.5 },
    //             },
    //         },
    //         detectRetina: false,
    //     }),
    //     [],
    // );

    return (
        <Particles
            id="tsparticles"
            init={particlesInit}
            loaded={particlesLoaded}
            options={{
                background: {
                    color: {
                        value: "#0d47a1",
                    },
                },
                fpsLimit: 120,
                interactivity: {
                    events: {
                        onClick: {
                            enable: true,
                            mode: "push",
                        },
                        onHover: {
                            enable: true,
                            mode: "repulse",
                        },
                        resize: true,
                    },
                    modes: {
                        push: {
                            quantity: 4,
                        },
                        repulse: {
                            distance: 200,
                            duration: 0.4,
                        },
                    },
                },
                particles: {
                    color: {
                        value: "#ffffff",
                    },
                    links: {
                        color: "#ffffff",
                        distance: 150,
                        enable: true,
                        opacity: 0.5,
                        width: 1,
                    },
                    move: {
                        direction: "none",
                        enable: true,
                        outModes: {
                            default: "bounce",
                        },
                        random: false,
                        speed: 6,
                        straight: false,
                    },
                    number: {
                        density: {
                            enable: true,
                            area: 800,
                        },
                        value: 80,
                    },
                    opacity: {
                        value: 0.5,
                    },
                    shape: {
                        type: "circle",
                    },
                    size: {
                        value: { min: 1, max: 5 },
                    },
                },
                detectRetina: true,
            }}
        />
    )
}

export default BackgroundParticles;